@import "../../../../config/variables";

#SearchCard {

  display: flex;
  flex-direction: row;

  .selectContainer {
    flex: 1;
    flex-direction: row;
  }

  .searchButton {
    width: 100px;
    background-color: $primaryColor;
    border-radius: 0;

    span {
      color: white;
    }
  }

  .css-yk16xz-control {
    border-radius: 0;

  }


  // MEDIA QUERIES ========================

  @media (max-width: 800px) {
    display: flex;
    flex-direction: column;

    .searchButton {
      width: 100%;
      background-color: $primaryColor;
      border-radius: 0;
      margin-top: 8px;

      span {
        color: white;
      }
    }

  }


}