@import "../../config/_variables.scss";

#ToolbarCmpnt {

  position: fixed;
  width: 100%;
  z-index: 10;
  height: $toolbar-height;
  top: 0;
  left: 0;

  .logo {
    height: 50px;
  }

  .appName {
    font-size: 28px;
    color: white;
  }

  #toolbarContainer {
    padding: 0 $layout-padding;
    font-size: 20px;
    color: white;
    background-color: transparent;
    z-index: 5;
    position: relative;
    height: 100%;
    align-items: center;
  }

  .showBackground {
    opacity: 1 !important;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
  }

  .background {
    background-color: $primaryColor !important;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
  }

  .transition {
    $transition-time: .25s;
    -webkit-transition: all $transition-time ease-in-out;
    -moz-transition: all $transition-time ease-in-out;
    -o-transition: all $transition-time ease-in-out;
    transition: all $transition-time ease-in-out;
  }

  .toolbarButton{

  }

  .toolbarButtonText{
    font-size: 16px;
    font-weight: 400;
    color: white;
  }

  // ========== MEDIA QUERIES ==========================

  @media (max-width: 450px) {

    .toolbarButtonText{
      font-size: 12px;
    }


    .logo {
      height: 40px;
    }

  }

}