@import '../../../../config/variables';

#Header {
  $headerHeight: 500px;

  #headerBackgroundImage {
    position: absolute;
    top: 0;
    width: 100%;
    background-image: url("../../../../assets/images/travel-map.jpg"); //, linear-gradient(90deg, #d4b59f, #766655 35%, #948b7f 100%);
    height: $headerHeight;
    background-position: right;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
  }

  #backgroundTint {
    position: absolute;
    height: $headerHeight;
    width: 100%;
    background: #c1af8452;
    top: 0;
    z-index: 2;
  }

  .headerContentContainer {
    z-index: 3;
    position: relative;
    padding: 25px 0;
    margin: $toolbar-height + 50px $layout-padding 0;

    display: flex;
    flex-direction: column;

  }

  .contentRight {
    flex: 1;
    display: flex;
    justify-content: center;
  }

  .contentLeft {
    flex: 1;
    padding: 15px 0 25px $layout-padding;
    display: inline-table;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;

    h1 {
      text-align: center;
      margin-bottom: 0;
      margin-top: 0;
      color: rgb(0, 0, 0);
      text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
    }

  }

  #searchCardContainer {
    padding: 8px;
    width: 100%;
    background-color: $backgroundColor;
  }

  // ========== MEDIA QUERIES ==========================


  @media (max-width: 950px) {

    .contentLeft {
      h1 {
        font-size: 1.4em;
      }
    }

  }

  @media (max-width: 700px) {

    .headerContentContainer {
      flex-direction: column;
      margin-top: $toolbar-height;
    }

    .contentLeft {
      margin-bottom: 16px;
      text-align: center;
      padding: 16px 0 16px $layout-padding;
    }

  }


}